export default {
	data() {
		return {
			userInfo: {},
			shop: [
				{
					icon: '../../static/phone.png',
					title: '绑定手机'
				},
				{
					url: '../integral/integral',
					icon: '../../static/integral.png',
					title: '积分兑换'
				},
				{
					icon: '../../static/order.png',
					title: '我的订单'
				},
				{
					url:'../login/login',
					icon: '../../static/grant.png',
					title: '授权'
				}
			]
		};
	},
	onLoad() {
		uni.getUserInfo({
			provider: 'weixin',
			success: res => {
				// console.log(res);
				this.userInfo = res.userInfo;
			}
		});
	}
};
